:root {
  --primary-color: #1899d5;
  --secondary-color: #e5e5e5;
  --accent-color: #e74c3c;
  --background-color: #ecf0f1;
  --text-color: #2c3e50;
  --header-color: #1abc9c;
  --footer-color: #34495e;
  --sucess-color: #2ab523;
}

.card-header {
  height: 2.5rem;
}

.card {
  border: .1875rem solid var(--primary-color);
}

.card h2 {
  color: var(--primary-color);
  padding: 1.25rem;
  font-size: 1.125rem;
}

.form-check-input {
  border: .125rem solid #000;
}

.container {
  max-width: 37.5rem;
}

.button-send {
  background-color: var(--sucess-color);
}

.text-success-custom {
  color: var(--sucess-color);
}

@media (width <= 400px) {
  .form-section {
    width: 100%;
  }
}

.footer {
  background-color: var(--primary-color);
  color: #fff;
  text-align: center;
  width: 100%;
  height: 3.125rem;
  padding: .625rem;
  display: block;
  position: fixed;
  bottom: 0;
  left: 0;
}
